import React from "react";
import propTypes from "prop-types";

import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import SearchIcon from 'material-ui/svg-icons/action/search';

export default class SearchBox extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            q: props.q,
            showError: false
        }
    }

    onValChange = (e) => {
        let currentTarget = e.target;
        const value = currentTarget.type === 'checkbox' ? currentTarget.checked : currentTarget.value;
        const name = currentTarget.name;

        this.setState({
            [name]: value
        })
    }

    handleSearch = () => {
        this.state.showError = this.state.search.length;
        this.props.handleSearch(this.state.search)
    }

    render() {
        return (
            <div className={this.props.className}>
                <TextField name="search" hintText="Type your query here" floatingLabelText="Query"
                  onChange={this.onValChange} value={this.state.search} />
                  <RaisedButton
                    label="Search"
                    labelPosition="before"
                    primary
                    onClick={this.handleSearch}
                    icon={<SearchIcon />}
                    />
            </div>
        )
    }
}

SearchBox.propTypes = {
    handleSearch: propTypes.func.isRequired,
    q: propTypes.string,
    className: propTypes.string
}



