import React from "react";
import propTypes from 'prop-types';
import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem'

export default class Dropdown extends React.PureComponent {
    constructor(props) {
        super(props)
        // this.state = { selVal: props.value };
    }

    onChange = (event, index, value) => {        
        this.props.onChangeCB && this.props.onChangeCB(event, value);
    }

    render() {
        return (<SelectField className={this.props.className} id="capital" value={this.props.value} errorText={!this.props.value && 'Should be selected'}
          floatingLabelText={this.props.label || ""} onChange={this.onChange}>
            <MenuItem value={null} primaryText="" />
            {this.props.options.map(function (option, ind) {
                return <MenuItem key={ind.toString()} value={option[this.props.valMap]} primaryText={option[this.props.keyMap]} />
            }, this)}
        </SelectField>)
    }
}
Dropdown.propTypes = {
    options: propTypes.array,
    keyMap: propTypes.string.isRequired,
    valMap: propTypes.string.isRequired,
    label: propTypes.string,
    onChangeCB: propTypes.func,
    className: propTypes.string,
    value: propTypes.any
}



