import React from 'react';
import ModalDialog from "../components/components/modalDialog.component";
import Dropdown from "../components/components/dropdown.component";
import { countryCapital } from "../yohodopo/appConstants";

import Toggle from 'material-ui/Toggle';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import { RadioButton, RadioButtonGroup } from 'material-ui/RadioButton';

import IconButton from 'material-ui/IconButton';
import MessageIcon from 'material-ui/svg-icons/communication/message';

const basicUI = function () {
    return <section className="child-center">
        <form name="basicComponents">
            <h3>Basic HTML Components</h3>
            <div>
                <TextField name="name" hintText="Enter your name" floatingLabelText="Name"
                  errorText={this.state.name.length ? '' : "This field is required"}
                  onChange={this.setValue} value={this.state.name} />
            </div>
            <div>
                <textarea placeholder="Enter your Address" value={this.state.address}
                  name="address" onChange={this.setValue} />
            </div>

            <div className="margin-top-15 ">
                <RadioButtonGroup name="gender" onChange={this.setValue}
                  valueSelected={this.state.gender} defaultSelected="F">
                    <RadioButton value="M" label="Male" />
                    <RadioButton value="F" label="Female" />
                </RadioButtonGroup>
            </div>
            <Dropdown className="margin-15 no-margin-left" label="Country" name="country"
              value={this.state.country} options={countryCapital}
              keyMap="name" valMap="cc" onChangeCB={this.setRespCapital} />
            <Dropdown label="Capital" name="capital" value={this.state.capital} options={countryCapital}
              keyMap="capName" className="margin-15" valMap="capCode" onChangeCB={this.setRespCountry} />
            <div className="checkBoxWrapper">
                <Toggle label="Forward screen data?" onToggle={this.setValue} name="passData"
                  value={this.state.passData} toggled={this.state.passData} />
            </div>
            <div id="serviceWorkers">
                <IconButton tooltip="Send Message" onClick={this.sendTBMessage}><MessageIcon /></IconButton>
                <h5>{this.state.reply}</h5>
            </div>
            <RaisedButton label="Open Dialog" secondary onClick={this.openModal} />
            <RaisedButton label="Submit" primary onClick={this.submitFormData} />
        </form>
        <ModalDialog open={this.state.showModal} onModalClose={this.closeModal}>
            <div>
                <h1>Hello dudus, this is modal-1 content</h1>
                <button onClick={this.openModal2}>Open Modal2</button>
                <button onClick={this.closeModal2}>Close Modal2</button>
                <ModalDialog open={this.state.showModal2} onModalClose={this.closeModal2}>
                    <h1>Hello dudus, this is modal-2 content</h1>
                </ModalDialog>
            </div>
        </ModalDialog>
    </section>
}

export default basicUI;



