const PageData = (state = {}, action) => {
    switch (action.type) {
        case 'GET_PAGE_DATA':
            return state;
        case 'SET_PAGE_DATA':
            return Object.assign({}, state, action.data);
        case 'EMPTY_PAGE_DATA':
            return {};
        default:
            return state;
    }
}

export default PageData;



